.login {
  margin-bottom: 250px;
}

.nick-input {
  margin-bottom: 10px;
  padding-left: 5px;
  width: 140px;
  height: 20px;
  border: 1px solid rgb(177, 155, 155);
  border-radius: 5px;
  font-size: 14px;
}

.input-hidden {
  display: none;
}

.login button {
  margin-left: 10px;
  margin-bottom: 10px;
  height: 24px;
  border-radius: 5px;
  font-size: 14px;
  color: rgb(78, 71, 33);
  background-color: rgb(211, 223, 153);
}

.nickname {
  margin-bottom: 10px;
  padding: 10px;
  color: rgb(223, 42, 10);
}

.nickname span {
  color: rgb(36, 110, 110);
}
