.message-list {
  margin: 0;
  padding: 0 auto;
}

.message-list-item {
  width: max-content;
  max-width: 50%;
  list-style-type: none;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px;
  border-radius: 10px;
  background-color: #ffffff;
}

.list-item-top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.from {
  color: crimson;
  padding: 0 15px;
}

.time {
  color: dimgray;
  font-size: 12px;
  line-height: 22px;
  margin-left: 20px;
}

.message {
  overflow-x: auto;
  font-size: 14px;
  padding: 5px 10px;
  border: 1px solid rgb(224, 213, 213);
  border-radius: 5px;
  color: darkslategray;
}
