.app {
  display: grid;
  grid-template-areas: 
    "header header"
    "main aside"
    "footer footer";
  grid-template-columns: 1fr 0.3fr;
  grid-template-rows: 80px auto 100px;
  grid-gap: 1%;
  height: 100vh;
}

header {
  grid-area: header;
  background-color: rgb(216, 230, 201)
}

main {
  background-color: rgb(219, 236, 240);
  grid-area: main;
  overflow: auto;
  border: 1px solid rgb(201, 208, 209);
}

aside {
  grid-area: aside;
  background-color: rgb(239, 241, 212);
}

footer {
  grid-area: footer;
  background-color: rgb(200, 207, 216);
}
